#contact {
    padding: 2.5%;
    background-color: var(--tertiary);
}
.link-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 5%;
}
.link {
    text-decoration: none;
    text-align: center;
    color: var(--text);
}
.link:hover {
    transform: scale(1.1);
    transition: all 0.1s ease-in;
    background: linear-gradient(to left, var(--primary), var(--text), var(--accent));
    color: transparent;
    background-clip: text;
    font-weight: bold;
}
.link img {
    width: 35px;
}
