.info-card {
    flex: 30%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding: 5%;
    margin: 5px;
    text-align: center;
    width: fit-content;
    height: fit-content;
    backdrop-filter: blur(24px);
}
.info-card:hover {
    transform: scale(1.025);
    background-color: var(--tertiary);
    transition: all 0.1s ease-in;
}

@media screen and (max-width: 820px){
    .info-card {
        flex: 100%;
    }
}