.no-page {
    color: var(--text);
    display: flex;
    flex-direction: column;
    align-items: center;
}
.no-page h1 {
    text-align: center;
}
.no-page h1 span{
    background: linear-gradient(to left, var(--primary), var(--text), var(--accent));
    color: transparent;
    background-clip: text;
    font-weight: bolder;
    font-size: larger;
}
.no-page a{
    width: fit-content;
    text-decoration: none;
    font-weight: bolder;
    font-size: larger;
    color: var(--text);
    padding: 2%;
    margin-top: 2%;
    background-size: 100% 100%;
    background-position: center;
    border: 3px solid;
    border-image-source: linear-gradient(to left, var(--primary), var(--text), var(--accent));
    border-image-slice: 1;
}