.game-container {
    text-align: start;
    display: flex;
    align-items: center;
}

.game-container span {
    color: var(--accent);
}

tictactoe {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}
.tictactoe {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;

    width: 500px;
    height: 500px;
}   

.tictactoebox {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    font-size: 100px;
    text-align: center;
}

.tictactoebox:nth-child(1) {
    border-right: 5px solid #091522;
    border-bottom: 5px solid #091522;
}
.tictactoebox:nth-child(2) {
    border-bottom: 5px solid #091522;
    border-left: 5px solid #091522;
    border-right: 5px solid #091522;
}
.tictactoebox:nth-child(3) {
    border-left: 5px solid #091522;
    border-bottom: 5px solid #091522;
}
.tictactoebox:nth-child(4) {
    border-top: 5px solid #091522;
    border-right: 5px solid #091522;
    border-bottom: 5px solid #091522;
}
.tictactoebox:nth-child(5) {
    border: 5px solid #091522;
}
.tictactoebox:nth-child(6) {
    border-top: 5px solid #091522;
    border-left: 5px solid #091522;
    border-bottom: 5px solid #091522;
}
.tictactoebox:nth-child(7) {
    border-right: 5px solid #091522;
    border-top: 5px solid #091522;
}
.tictactoebox:nth-child(8) {
    border-top: 5px solid #091522;
    border-left: 5px solid #091522;
    border-right: 5px solid #091522;
}
.tictactoebox:nth-child(9) {
    border-left: 5px solid #091522;
    border-top: 5px solid #091522;
}

.turns {
    margin: 15px;
}
.turns div {
    width: 120px;
    font-size: xx-large;
    font-weight: bold;
    text-align: center;
    background-color: var(--tertiary);
}
.active-turn {
    background: linear-gradient(to left, var(--primary), var(--text), var(--accent));
    color: transparent;
    background-clip: text;
    background-size: 100% 100%;
    background-position: center;
    border: 3px solid;
    border-image-source: linear-gradient(to left, var(--primary), var(--text), var(--accent));
    border-image-slice: 1;
}

@media screen and (max-width: 820px){
    .game-container {
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100vw;
    }
    tictactoe {
        padding-top: 10%;
        height: fit-content;
        display: flex;
        align-items: start;
        justify-content: center;
    }
    .tictactoe {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr;
    
        width: 100vw;
        height: 100vw;
    }   
    .turns {
        display: none;
    }
}