.info-panel-bg {
    z-index: 4;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;

    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.609);
}
.info-panel {
    width: fit-content;
    min-width: 600px;
    height: fit-content;
    min-height: calc(100vh/2);
    background-color: var(--tertiary);
    padding: 10px;
}
.info-header {
    display: grid;
    grid-template-columns: 3fr 1fr;
}
.info-header div {
    display: flex;
}
.info-header div:nth-child(1) {
    justify-content: start;
    align-items: center;
    gap: 10px;
}
.info-header div:nth-child(2) {
    justify-content: end;
}
.info-body {
    color: var(--text);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 5%;
}
.info-body tr, .info-body thead{
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;
}
.info-body thead {
    border-bottom: 3px solid var(--text);
}
.info-body tr td:nth-child(1), .info-body thead td:nth-child(1){
    text-align: start;
}
.info-body tr td:nth-child(2), .info-body thead td:nth-child(2){
    text-align: end;
}

.info-panel button {
    border: none;
    background-color: transparent;
}