.parent-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.side-border {
    background-size: 100% 100%;
    background-position: center;
    border-left: 3px solid;
    border-right: 3px solid;
    border-image-source: linear-gradient(to top, var(--primary), var(--text), var(--accent), var(--text), var(--primary));
    border-image-slice: 1;
}
#home {
    height: fit-content;
    background-image: url('../../assets/svg/wave.svg');
    background-repeat: no-repeat;
    background-size: cover;
}
.introduction {
    width: 100%;
    padding: 5% 0 5% 0;
    display: flex;
}
.profile {
    width: 100%;
    padding: 5%;
    font-weight: bolder;
    font-size: larger;
}
.profile h1:nth-child(2), .profile h1:nth-child(4){
    margin-top: 5%;
    font-style: italic;
    font-size: larger;
}
.profile h1:nth-child(3){
    font-size: xx-large;
}
.profile h1:nth-child(1){
    text-align: center;
}
.profile h1:nth-child(4), .profile h1:nth-child(5){
    text-align: end;
}
.big-text {
    font-size: 4rem;
}

@media screen and (max-width: 820px){
    .parent-container {
        width: 100vw;
    }
    .introduction {
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .profile {
        padding: 5%;
        font-weight: bolder;
        font-size: medium;
    }
}