.post {
    margin: 5px;
}

.post-footer {
    text-align: center;
    background-color: var(--tertiary);
    padding: 2%;
}
.post-header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 2%;
    background-color: var(--tertiary);
}
.post-header div {
    display: flex;
    align-items: center;
}
.post-header div:nth-child(2) {
    justify-content: end;
}
.post-header img {
    width: 50px;
}

.post-content .image-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.post-content .image-container img {
    width: 100%;
    max-width: calc(100%/3);
    flex: 30%;
}