* {
  margin: 0;
  padding: 0;
  --outline: 1px solid red;
  box-sizing: border-box;
}

:root {
  --text: #d7e9fe;
  --background: #00070f;
  --primary: #fc64f7;
  --secondary: #1e1001;
  --accent: #05e60c;
  --tertiary: #091522;  
}

.gradient-text {
  background: linear-gradient(to left, var(--primary), var(--text), var(--accent));
  color: transparent;
  background-clip: text;
}
.gradient-border {
  background-position: center;
  border: 3px solid;
  border-image-source: linear-gradient(to top, var(--primary), var(--text), var(--accent), var(--text), var(--primary));
  border-image-slice: 1;
}

::-webkit-scrollbar {
  position: fixed;
  left: 0;
  top: 0;
  width: 4px;
  background-color: var(--background);
}
::-webkit-scrollbar-thumb {
  border-radius: 50px;
  background: linear-gradient(to bottom, var(--accent), var(--text), var(--primary));
}
